.btn {
  background-color: #d81b60;
  color: #fff;
  height: 50px;
  width: 230px;
  line-height: 50px;
  border-radius: 25px;
  display: inline-block;
  text-transform: uppercase;
  font-weight: 700;
  text-align: center;
  text-decoration: none;
  border: 2px solid transparent;
  box-shadow: 0px 1px 0px rgba(0, 0, 0, 0.3);
  position: relative; }
  .btn:active {
    top: 1px;
    box-shadow: none; }
  .btn:hover, .btn:focus {
    outline: 0px;
    color: #fff;
    background-color: #f06292;
    border-color: #d81b60; }

pre {
  cursor: text;
  margin: 1em;
  box-shadow: inset 2px 2px 10px 0px #212121, 1px 1px 0px #fff;
  padding: 1em;
  font-family: monospace;
