  color: #f5f5f5;
  border-radius: 5px;
  font-size: 11px; }

.modal-backdrop {
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  left: 0px;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: opacity 0.5s ease;
  top: 0px; }
  .modal-backdrop.active {
    opacity: 1; }
    .modal-backdrop.active .modal {
      top: 50%; }
  .modal-backdrop .modal {
    transition: top 0.5s ease;
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: calc(100% - 2em);
    max-width: 50em; }
    .modal-backdrop .modal .modal-body {
      text-align: center; }
      .modal-backdrop .modal .modal-body img {
        max-width: 100%; }

section {
  padding: 4em 0em;
  position: relative; }
  section ul, section ol {
    padding-left: 2em; }
  section ul {
    list-style-type: disc; }
  section ol {
    list-style-type: decimal; }
  section img {
    display: block;
    border: 2px solid #424242;
    margin: 2em auto;
    max-width: 300px;
    cursor: pointer;
    transition: max-width 0.5s ease;
    user-select: none; }
    section img:hover {
      border-color: #d81b60; }
  section:after, section:before {
    position: absolute;
    left: 0px;
    content: '';
    display: block;
    width: 100%;
    height: 1px; }
  section:after {
    bottom: 0px;
    background-image: radial-gradient(circle, rgba(120, 120, 120, 0.2), rgba(120, 120, 120, 0)); }
  section:before {
    bottom: -1px;
    background-image: radial-gradient(circle, rgba(255, 255, 255, 0.8) 50%, rgba(255, 255, 255, 0)); }
  section.banner {
    padding: 0em;
    height: 600px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: #fff;
    background-position: 50% 0%;
    background-size: auto 600px;
    background-attachment: fixed; }
    section.banner.light {
      color: #424242; }
    @media (max-height: 800px) {
      section.banner {
        height: 500px;
        background-size: auto 500px; } }
    @media (max-height: 600px) {
      section.banner {
        height: 400px;
        background-size: auto 400px; } }
    @media (max-height: 500px) {
      section.banner {
        height: 300px;
        background-size: auto 300px; } }
    section.banner .container {
      position: relative; }
      section.banner .container h1, section.banner .container h2, section.banner .container p {
        max-width: 50%; }
  section.rich-text-section .container {
    max-width: 600px; }
  section.buttons .container {
    text-align: center; }
    section.buttons .container .button-container .btn {
      margin: 1em; }
    section.buttons .container .link-container {
      margin-top: 1em; }
      section.buttons .container .link-container a {
        margin: 0em 1em; }
  section.images .container {
    text-align: center; }
    section.images .container img {
      display: inline;
      border: 0px;
      margin: 2em 1em 0em 1em;
      line-height: 1em;
      max-width: 14em; }
  section.child-links .container {
    max-width: 600px; }
    section.child-links .container a {
      display: block; }
  section .container {
    position: relative; }

nav {
